.profile-page-artifact{width:var(--artifact-width);border:1px solid rgba(0,0,0,0);margin:10px;background-color:gray;background-image:var(--artifact-bg);background-size:cover;background-position:center;background-repeat:no-repeat;color:#000;position:relative;aspect-ratio:.895;font-family:"GenshinFont";transition:transform .2s}.profile-page-artifact:hover{transform:scale(1.05)}.profile-page-artifact .artifact-equipped-char{position:absolute;bottom:calc(var(--artifact-width)/.86);right:0;z-index:-1;width:var(--artifact-width);display:flex;justify-content:flex-end;align-items:flex-end;flex-wrap:wrap}.profile-page-artifact .artifact-equipped-char img{flex-grow:0;flex-shrink:0;background-color:#000;width:35px;height:35px;border-radius:35px;margin-left:-10px}.profile-page-artifact .artifact-name{color:#fff;margin-left:calc(var(--artifact-width)/14.3);margin-top:calc(var(--artifact-width)/66.667);font-size:calc(var(--artifact-width)/14.3);white-space:nowrap;text-overflow:ellipsis;overflow:hidden;margin-right:calc(var(--artifact-width)/100)}.profile-page-artifact .artifact-crit-value{color:#b5a6a0;position:absolute;top:calc(var(--artifact-width)/6.66);margin-left:calc(var(--artifact-width)/14.3);font-size:calc(var(--artifact-width)/20)}.profile-page-artifact .artifact-stat-name{color:#b5a6a0;margin-left:calc(var(--artifact-width)/14.3);margin-top:calc(var(--artifact-width)/5.5);font-size:calc(var(--artifact-width)/20)}.profile-page-artifact .artifact-stat-value{color:#fff;margin-left:calc(var(--artifact-width)/14.3);font-size:calc(var(--artifact-width)/9.09)}.profile-page-artifact .artifact-rarity{margin-left:calc(var(--artifact-width)/14.3)}.profile-page-artifact .artifact-rarity img{width:calc(var(--artifact-width)/17);margin-right:3px}.profile-page-artifact .artifact-level{color:#fff;position:absolute;top:calc(var(--artifact-width)/1.59);margin-left:calc(var(--artifact-width)/14.3);font-size:calc(var(--artifact-width)/22.22)}.profile-page-artifact .substats{display:flex;flex-direction:column;gap:3px;position:absolute;margin-left:calc(var(--artifact-width)/18.2);font-size:calc(var(--artifact-width)/19);top:calc(var(--artifact-width)/1.39);opacity:.8}.profile-page-artifact .substats>.substat::before{content:"Â·";margin-right:calc(var(--artifact-width)/200)}.profile-page-artifact .artifact-icon{position:absolute;top:calc(var(--artifact-width)/8);right:calc(var(--artifact-width)/20);width:calc(var(--artifact-width)/2.353)}.profile-page-artifact .artifact-set-name{color:#5cb256;position:absolute;font-size:calc(var(--artifact-width)/19);margin-left:calc(var(--artifact-width)/14.3);bottom:calc(var(--artifact-width)/70)}@keyframes cyanGlow{100%,0%{box-shadow:0 0 0 1px aqua,0 0 8px 3px aqua}50%{box-shadow:0 0 0 1px aqua,0 0 8px 3px rgba(0,255,255,.247)}}.unicorn-artifact{animation:cyanGlow 2s infinite}.unicorn-artifact .compact-artifact-crit-value{color:aqua}.upper-diamond-artifact{box-shadow:0 0 0 1px #ffd900,0 0 8px 3px rgba(255,217,0,.623)}.upper-diamond-artifact .compact-artifact-crit-value{color:#ffd900}.diamond-artifact{box-shadow:0 0 0 1px orange,0 0 4px 2px rgba(255,165,0,.623)}.diamond-artifact .compact-artifact-crit-value{color:orange}.good-artifact{box-shadow:0 0 0 1px #c266ff}.good-artifact .compact-artifact-crit-value{color:#c266ff}.ok-artifact{box-shadow:0 0 0 1px #66a3ff}.ok-artifact .compact-artifact-crit-value{color:#66a3ff}.poo-artifact{box-shadow:0 0 0 1px gray}.poo-artifact .compact-artifact-crit-value{color:gray}